import { Http, URLSearchParams} from '@angular/http';
import { Injectable } from '@angular/core';
import "rxjs/add/operator/map";

import { URL_SERVICES } from '../../config/url.services';
import { Platform, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Device } from '@ionic-native/device';
/*
  Generated class for the UserProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class UserProvider {

  token:string;
  public user_id:string;
  macaddress_device:string;

  constructor(public http:Http,
              private alertCtrl:AlertController,
              private plataform:Platform,
              private storage:Storage,
              private device:Device) {
    console.log('Hello UserProvider Provider');
  }

  login_user(folio:string, password:string){
    let data = new URLSearchParams();
    let url = URL_SERVICES + "/USER_/ValidateUser";



    console.log(folio);
    console.log(password);
    data.append("FOLIO", folio);
    data.append("PASSWORD", password);

    console.log(this.device.uuid);

    data.append("MACADDRESS_DEVICES", this.device.uuid);

    return this.http.post(url, data)
                    .map (resp => {

                      let result = resp.json();

                      console.log(result);

                      if( result.error == true ){
                      this.alertCtrl.create({
                        title:"error",
                        subTitle: result.message,
                        buttons: ["ok"]
                      }).present();
                      }else{
                        this.token = result.TOKEN;
                        this.user_id = result.USER_ID;

                        this.save_storage();
                      }
                    });
  }

  close_session(){
    this.token = null;
    this.user_id = null;

    //Save in storage
    this.save_storage();
  }


  private save_storage(){
    if(this.plataform.is("cordova")){
      //Device
      this.storage.set('token', this.token);
      this.storage.set('user_id', this.user_id);
    }else{
      if(this.token){
        localStorage.setItem("token", this.token);
        localStorage.setItem("user_id", this.user_id);
      }else{
        localStorage.removeItem("token");
        localStorage.removeItem("user_id");
      }
    }
  }

  load_storage(){
    let promise = new Promise((resolve, reject) => {
      if(this.plataform.is("cordova")){
          //Device
          this.storage.ready()
                      .then(()=>{
                        this.storage.get("token")
                        .then((token)=>{
                          if(token){
                            this.token = token;
                            console.log("Token: " + token);
                          }
                        });

                        this.storage.get("user_id")
                        .then((user_id)=>{
                          if(user_id){
                            this.user_id = user_id;
                            console.log("user_id" + this.user_id)
                          }
                        });

                        resolve();
                      });
      }else{
        // Browser
        if(localStorage.getItem("token")){
          this.token = localStorage.getItem("token");
          this.user_id = localStorage.getItem("user_id");

          console.log("Browser: " + this.user_id + " : " + this.token);
        }
      }
    })
  }

  validate_session():boolean{
    if( this.token ){
      return true;
    }else{
      return false;
    }
  }
}



